.App {
  text-align: center;
}
* {
  border-collapse: collapse;
  margin:0rem;
  padding:0rem;
  text-decoration: none;
  color: black;
  list-style: none;
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: inherit;
      /* box-sizing: border-box; */

}
body {
  margin: 0;
}
.web-container {
  display: flex;
  /* gap: 1rem; */
  height: 100vh;
  width: 100%;
  /* border: green 2px solid; */
  box-sizing: border-box;

}
.web-side-nav {
  width: fit-content;
  height: 100vh;

}
.web-content {
  width: 100%;
  height: 100%;
  display: grid;
  box-sizing: border-box;
  /* border: rgb(96, 109, 224) 2px solid; */

}