/*공통 레이아웃 속성*/





.content {
    padding: 2rem;
    background-color: #F6F8FB;
    /* box-sizing: border-box; */
  /* border: rgb(96, 109, 224) 2px solid; */
    overflow: auto;
}

.content > .content-title {
    font-size: 2rem;
    font-weight: bold;
    size: 2rem;
    margin-bottom: 2rem;
    color: #73778D;

}
.content > .content-title > .icon {
    fill: #73778D;
}
.icon {
    margin-right: 0.5rem;
}
.circle {
    border-radius: 50%;
    border-width: thick;
    border-style: solid;
    /* padding: 1rem; */
}
.float {
    float: left;
}
.round-button {
    border-radius: 25px;
    border-style: none;
    padding: 0.5rem 3rem;
    font-size: 1rem;
}
.center-align {
    text-align: center;
}
.left-align {
    text-align: left;
}
.right-align {
    text-align: right;
}
.grid-container {
    display: grid;
    height: 90%;            /*100퍼로 하면 아래 내려감. 확인예쩡*/ 
    /* border: 1px solid rgb(204, 24, 93); */
    box-sizing: inherit;    /*부모 기준으로 크기 정함*/
    gap: 1.5rem;
}

.grid-item {
    background-color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: 1rem;
    /* border: 1px solid #0f0; */
}

.grid-title {
    border-radius: 1rem 1rem 0rem 0rem; /*위쪽 두군대 round처리*/
    margin: -1rem -1rem 0.5rem -1rem;    /*grid-item의 padding을 bottom빼고 무시*/
    text-align: center;
    font-size: 1.1rem;
    padding: 0.4rem;
}

.data-reload{   /*데이터 불러오기 실패시 버튼*/
    position: absolute;
    border: 1px solid #f00;
    font-weight: bold;
    background-color: #f00;
    color: #fff;
    top: 30%;
    left: 30%;
    padding: 2rem;
    font-size: 2rem;
}

/*scroll*/
.item-scroll {
    overflow:auto;  /*scroll*/
    /* height: calc( 100% - 2rem); */
    height: 100%;
    /* border: 1px solid rgb(24, 57, 204); */
    
}
.item-scroll::-webkit-scrollbar {
    width: 0.3rem;
    background-color: #ffffff00;
}
.item-scroll::-webkit-scrollbar-thumb {
    background-color: #C2CFE0;
    border-radius:2rem;
}
.item-scroll::-webkit-scrollbar-track {
    background-color: #ffffff00;
    border-radius: 2rem;
}
/* ---- modal content ---- */

#modal > #overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .3);
    z-index: 1000;
}

#modal > #modal-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* padding: 1rem 2rem; */
    width : 35%;
    /* height : 20%; */
    height : max-content;
    z-index: 1000;
    font-weight: bold;
    border-radius: 1rem;
}
#modal #modal-title{
    border-radius: 2rem 2rem 0rem 0rem; /*위쪽 두군대 round처리*/
    /* margin: -1rem -2rem 0.5rem -2rem;    grid-item의 padding을 bottom빼고 무시 */
    text-align: center;
    font-size: 1.3rem;
    padding: 0.7rem;
    border-bottom: 0.1rem solid #ccc;

}
#modal #modal-content{
    height: 40%;
    padding: 2rem 1rem;
    font-size: 1.1rem;
    text-align: center;
    overflow:auto;  /*scroll*/
}
    /*scroll*/
#modal-content::-webkit-scrollbar {
    width: 0.5rem;
    background-color: #fff;
}
#modal-content::-webkit-scrollbar-thumb {
    background-color: #8CB5E4;
    border-radius:2rem;
}
#modal-content::-webkit-scrollbar-track {
    background-color: #dadada;
    border-radius: 2rem;
}
#modal #button-div{
    border: 1px solid #f00;
    padding: 0rem -2rem -1rem -2rem;    

}
#modal button{
    /* margin: 0rem -2rem -1rem -2rem;     */
    width: 50%;
    height: 1.7rem;
    /* padding: 0.2rem 2rem; */
    /* border-radius: 0.5rem; */
    font-weight: bold;

}
#modal #ok-button{
    float: right;
    border: 0rem solid #336CFB;
    border-top: 0.1rem solid #336CFB;

    border-radius: 0rem 0rem 1rem 0rem; /*위쪽 두군대 round처리*/

}

#modal #cancle-button{
    float: left;
    border: 0rem solid #73778D;
    border-top: 0.1rem solid #73778D;
    border-radius: 0rem 0rem 0rem 1rem; /*위쪽 두군대 round처리*/

}


