    margin: 0.5rem;
}
#map #item-serving #arrow-right-div{
    display: inline-block;
    animation: mymove infinite;
    animation-duration: 1s;
}
#map #item-serving #arrow-right-div p:last-child{
    position: absolute;
}
#map #item-serving #arrow-right{
	width: 0;
	height: 0;
	border-top: 0.4rem solid transparent;
	border-bottom: 0.4rem solid transparent;
	border-left: 0.4rem solid #336CFB;/* 화살표 */
    display: inline-block;
}

@keyframes mymove {
from {margin-left: 0px;}
to {margin-left: 0.3rem;}
}
